// EditUserDialog.js
import React from "react";
import { Formik } from "formik";
import * as Yup from "yup";
import AuthService from "./AuthService";
import PropTypes from "prop-types";
import withStyles from "@material-ui/core/styles/withStyles";
import {
  Avatar,
  Button,
  CssBaseline,
  FormControl,
  Input,
  InputLabel,
  Paper,
  Typography
} from "@material-ui/core";
import { red } from "@material-ui/core/colors";

const styles = theme => ({
  main: {
    width: "auto",
    display: "block", // Fix IE 11 issue.
    marginLeft: theme.spacing.unit * 3,
    marginRight: theme.spacing.unit * 3,
    [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
      width: 400,
      marginLeft: "auto",
      marginRight: "auto"
    }
  },
  paper: {
    marginTop: theme.spacing.unit * 8,
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    padding: `${theme.spacing.unit * 2}px ${theme.spacing.unit * 3}px ${theme
      .spacing.unit * 3}px`
  },
  avatar: {
    margin: theme.spacing.unit,
    backgroundColor: theme.palette.secondary.main
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing.unit
  },
  buttonHolder: {
    width: "100%",
    display: "flex",
  },
  submit: {
    marginTop: theme.spacing.unit * 3,
  },
  inputErrors: {
    color: 'red',
    fontSize: '10px'
  }
});

const signupValidationSchema = Yup.object().shape({
  email: Yup.string()
    .email('Inserte un email vlido')
    .required('Inserte su email'),
  firstName: Yup.string()
    .min(2, 'Inserte su nombre, ms de 2 caracteres')
    .max(2, 'Inserte su nombre, menos de 40 caracteres')
    .required('Inserte su nombre'),
  lastName: Yup.string()
    .min(2, 'Inserte su apellido, ms de 2 caracteres')
    .max(2, 'Inserte su apellido, menos de 40 caracteres')
    .required('Inserte su apellido'),
  password: Yup.string()
    .min(8, "Inserte ms de 8 caracteres")
    .max(40, "Inserte menos de 40 caracteres")
    .required("Inserte una contrasea"),
  passwordConfirmation: Yup.string()
    .oneOf([Yup.ref('password'), null], 'Las contraseas deben de ser iguales')
});

const Signup = props => {
  let initialValues = { email: "", password: "", passwordConfirmation: "", firstName:"", lastName:"" };
  let service = new AuthService();
  let { classes, getUser } = props;
  return (
    <main className={classes.main}>
      <CssBaseline />
      <Paper className={classes.paper}>
        <Typography component="h1" variant="h5">
          Crear cuenta
        </Typography>
        <Formik
          initialValues={initialValues}
          onSubmit={({actions}, values) => {
            service
              .signup(values)
              .then(response => {                
                getUser(response.user);
              })
              .catch(error => {
                console.log(error)
                actions.setErrors({error});
              });
          }}
          validationSchema={signupValidationSchema}
          render={({
            values,
            errors,
            touched,
            handleBlur,
            handleChange,
            handleSubmit,
            isSubmitting
          }) => (
            <form className={classes.form} onSubmit={handleSubmit}>
              <FormControl margin="normal" fullWidth>
                <InputLabel htmlFor="username">Email</InputLabel>
                <Input
                  type="email"
                  name="email"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.email}
                />
                {errors.email && touched.email && (
                  <div className={classes.inputErrors}>{errors.email}</div>
                )}
              </FormControl>
              <FormControl margin="normal" fullWidth>
              <InputLabel htmlFor="username">Nombre</InputLabel>
                <Input
                  type="firstName"
                  name="firstName"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.firstName}
                />
                {errors.firstName && touched.firstName && (
                  <div className={classes.inputErrors}>{errors.firstName}</div>
                )}
              </FormControl>
              <FormControl margin="normal" fullWidth>
              <InputLabel htmlFor="username">Apellido</InputLabel>
                <Input
                  type="lastName"
                  name="lastName"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.lastName}
                />
                {errors.lastName && touched.lastName && (
                  <div className={classes.inputErrors}>{errors.lastName}</div>
                )}
              </FormControl>
              <FormControl margin="normal" fullWidth>
              <InputLabel htmlFor="username">Contrasea</InputLabel>
                <Input
                  type="password"
                  name="password"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.password}
                />
                {errors.password && touched.password && (
                  <div className={classes.inputErrors}>{errors.password}</div>
                )}
              </FormControl>
              <FormControl margin="normal" fullWidth>
              <InputLabel htmlFor="username">Confirmar contrasea</InputLabel>
                <Input
                  type="password"
                  name="passwordConfirmation"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.passwordConfirmation}
                />
                {errors.passwordConfirmation && touched.passwordConfirmation && (
                  <div className={classes.inputErrors}>{errors.passwordConfirmation}</div>
                )}
              </FormControl>
              <div className="buttonHolder">
              <Button 
                variant="contained"
                color="primary"
                className={classes.submit}
                type="submit" disabled={isSubmitting}>
                Crear cuenta
              </Button>
              {errors.error && <div className={classes.inputErrors}>{errors.error}</div>}
              </div>
            </form>
          )}
        />
      </Paper>
    </main>
  );
};

Signup.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withStyles(styles)(Signup);
